@import "variables";

/* loader */
.loaderArea {
	background: rgba(0,75,147,1);
	overflow: hidden;
	position: fixed;
	left: 0;
	top: 0;
	right:0;
	bottom:0;
	z-index: 100000;
}
.loader{
	position:absolute;
	top:50%;
	left:50%;
	transform:translate(-50%, -50%);
	width:350px;
	height:350px;
	display: flex;
	align-items: center;
	justify-content: center;
	z-index: 1;
	&:after{
		content: '';
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		margin-top: -40px;
		background: url(../images/loader-bg.png) center center no-repeat;
		background-size: contain;
		z-index: -1;
        animation: rotating 10s linear infinite;
	}
}
@keyframes rotating {
  	from {transform:rotate(0deg);}
    to {transform:rotate(360deg);} }

@media only screen and (max-width: 450px) {
	.loader{
		width:250px;
		height:250px;
	}
}