@import "variables";

.widget-button{
	position: fixed;
	right: 10px;
	bottom: 64px;
    border: 1px solid #fff;
    z-index: 999;
    cursor: pointer;
    box-shadow: 0 0 10px rgba(0,0,0,.48);
    border-radius: 5px;
    background: rgba(0,75,147,0.75) !important;
    font-size: 15px;
    letter-spacing: 1.5px;
    color: #fff;
    font-family: $font-1;
    padding: 5px 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    .widget-button-logo{
    	width: 22px;
    	height: 16px;
    	margin-right: 5px;
    	display: block;
    	background: url('../images/icon-4.svg') center center no-repeat;
    }
    &:before{
		content: '';
		position: absolute;
		left: 50%;
		top: 100%;
		z-index: 3;
		transform: translateX(-50%);
		border: 10px solid transparent;
		border-top: 10px solid $color-1;
		border-bottom: none;
		margin-top: -1px;
	}
	&:after{
		content: '';
		position: absolute;
		left: 50%;
		top: 100%;
		z-index: 1;
		transform: translateX(-50%);
		border: 10px solid transparent;
		border-top: 10px solid #fff;
		border-bottom: none;
		margin-top: 0px;
	}
}
@media (max-width: 1810px) {
	.widget-button{
		bottom: 110px;
	}
}
@media (max-width: 767px) {
	.widget-button{
		display: none;
	}
}