@import "variables";

/* first-screen */
.first-screen{
	background: url(../images/bg-1.jpg) center bottom no-repeat;
	background-size: cover;
	position: relative;
	.container-sm{
		max-width: 780px;
		margin: 0 auto;
		position: relative;
		z-index: 10;
		padding-bottom: 174px;
		padding-top: 15px;
	}
	.logo-wrap{
		padding-top: 13px;
	}
}

/* */
.leaves{
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	z-index: 1;
	.item-l{
		position: absolute;
		left: 87px;
		top: 65px;
	}
	.item-r{
		position: absolute;
		right: -53px;
		top: 60px;
	}
}

.parallax-layer{
	transform-origin: center;
}

/* */
.pasta-images{
	position: absolute;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 5;
	.item-l{
		position: absolute;
		left: 15px;
		bottom: 116px;
		z-index: 3;
	}
	.item-r{
		position: absolute;
		right: -88px;
		bottom: -17px;
		z-index: 1;
	}
	&.active{
		.item-l{
			animation-duration: 1s;
			animation-fill-mode: both;
			animation-name: translateLeft;
		}
		.item-r{
			animation-duration: 1s;
			animation-fill-mode: both;
			animation-name: translateRight;
		}
	}
}
@keyframes translateLeft {
	0% {
		transform: translateX(-50vw);
	}
	100% {
		transform: translateX(0);
	}
}
@keyframes translateRight {
	0% {
		transform: translateX(50vw);
	}
	100% {
		transform: translateX(0);
	}
}

/* */
.main-title{
	text-align: center;
	color: #fff;
	font-size: 42px;
	font-family: $font-1b;
	text-transform: uppercase;
	text-shadow: 3px 3px 0 rgba(0,0,0,0.43);
	letter-spacing: 2px;
	line-height: 1.45em;
	margin-top: -35px;
	margin-bottom: 50px;
}

/* */
.key-form-wrap{
	position: relative;
	max-width: 490px;
	margin: 0 auto 25px;
	border-radius: 0 47px 47px 0;
}

.key-form{
	position: relative;
	.key-input{
		background: #fff;
		height: 94px;
		border-radius: 47px;
		font-size: 20px;
		text-transform: uppercase;
		color: #464646;
		font-family: $font-1b;
		padding: 0 115px 0 50px;
	}
	.key-btn{
		position: absolute;
		top: 5px;
		right: 5px;
		height: 84px;
		width: 84px;
		border-radius: 50%;
		border: none;
		background: $color-3 url(../images/key-btn.png) center center no-repeat;
	}
}
.over-key{
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 94px;
	background: $color-3;
	border-radius: 47px;
	text-align: center;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	align-content: center;
	padding-top: 16px;
	transition: transform 0.5s;
	cursor: pointer;
	p{
		margin: 0;
		width: 100%;
		display: block;
		color: #fff;
		font-family: $font-1ib;
		font-size: 12px;
		letter-spacing: 1px;
		text-shadow: 1px 1px 0 rgba(0,0,0,0.61);
		&.title{
			font-size: 28px;
			font-family: $font-1b;
			text-transform: uppercase;
			text-shadow: 3px 3px 0 rgba(0,0,0,0.61);
			line-height: 1.1em;
		}
	}
	&:after{
		content: '';
		position: absolute;
		left: -15px;
		top: -67px;
		width: 170px;
		height: 161px;
		background: url(../images/key-bg.png) center top no-repeat;
		background-size: cover;
		opacity: 1;
		transition: opacity 0.3s;
	}
}

.key-form-wrap.active{
	overflow:hidden;
	.over-key{
		transform: translateX(100%);
		transition-delay: 0.3s;
		&:after{
			opacity: 0;
		}
	}
}

/* stages */
.stages-box{
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	height: 174px;
	z-index: 10;
	padding: 42px 0 25px;
	background: url(../images/stages-bg.png) center bottom repeat-x;
}
.stages-wrap{
	margin-left: -25px;
	margin-right: -25px;
}
.stages{
	display: flex;
	.slick-track{
		display: flex;
		align-items: center;
		padding-top: 15px;
	}
	.stage{
		color: #fff;
		display: flex;
		align-items: center;
		padding: 0px 25px;
		position: relative;
		font-size: 18px;
		letter-spacing: 1px;
		&:after{
			content: '';
			position: absolute;
			top: 50%;
			right: -3px;
			width: 20px;
			height: 20px;
			margin-top: -10px;
			border-top: 1px solid #fff;
			border-right: 1px solid #fff;
			transform: rotate(45deg);
			transform-origin: center;
		}
		.image{
			margin-right: 20px;
			img{
				margin: 0;
				display: block;
			}
		}
		p{
			margin: 0;
		}
		.hint{
			border-bottom: 1px dashed #fff;
		}
		strong{
			letter-spacing: 1px;
		}
		&.stage-3{
			.image{
				margin-top: 0px;
			}
		}
		&.stage-4{
			.image{
				margin-top: -15px;
				margin-right: 2px;
			}
		}
		&:last-child{
			&:after{
				display: none;
			}
		}
	}
}

/* next-block */
.next-block{
	position: absolute;
	left: 50%;
	top: 0;
	transform: translateX(-50%);
	display: block;
	color: #fff;
	padding: 9px 10px 19px;
    letter-spacing: 2px;
	&:before, &:after{
		content: '';
		position: absolute;
		bottom: 5px;
		left: 50%;
		width: 20px;
		height: 20px;
		margin-left: -10px;
		border-bottom: 1px solid #fff;
		border-right: 1px solid #fff;
		transform: rotate(45deg);
		transform-origin: center;
		animation-duration: 1s;
		animation-fill-mode: both;
		animation-iteration-count: infinite;
		animation-name: fadeIn;
	}
	&:before{
		border-bottom-color: rgba(256,256,256,0.34);
		border-right-color: rgba(256,256,256,0.34);
		bottom: 12px;
		animation-delay: 0.5s;
	}
	&:hover{
		color: #fff;
		text-decoration: none;
	}
}
@keyframes fadeIn {
	0% {
		opacity: 1;
	}
	50% {
		opacity: 0.7;
	}
	100% {
		opacity: 1;
	}
}

@media (max-width: 1023px), (max-height: 700px){
	.section{
		min-height: 100vh;
		min-height: calc(var(--vh, 1vh) * 100);
		overflow: hidden;
	}
	.next-block{
		display: none;
	}
}

