@import "variables";

/* modal */
.modal-open{
	overflow: hidden;
	#wrapper{
		filter: blur(7px);
	}
	.modal{
		overflow-x: hidden;
		overflow-y: auto;
	}
}
 
.modal{
	position: fixed;
	top: 0;
	left: 0;
	z-index: 1050;
	display: none;
	width: 100%;
	height: 100%;
	overflow: hidden;
	outline: 0;
}

.modal-dialog {
	position: relative;
	pointer-events: none;
	max-width: 750px;
	margin: 140px auto 0.5rem;
}

.modal.fade .modal-dialog {
	transition: transform 0.3s ease-out;
	transform: translate(0, -50px);
}
.modal.show .modal-dialog {
	transform: translate(0,0);
}

.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1040;
  width: 100vw;
  height: 100vh;
  background-color: #000;
  transition-duration: 0.2s;
}
.modal-backdrop.fade {
  opacity: 0;
}
.modal-backdrop.show {
  opacity: 0;
}

.modal button.close {
	position: absolute;
	top: 20px;
	right: 20px;
	width: 35px;
	height: 35px;
	margin: 0;
	opacity: 1;
	background: none;
	border: none;
	z-index: 10;
	transition-duration: 0.2s;
	&:before{
		content: '';
		width: 30px;
    	height: 1px;
	    background: #fff;
	    position: absolute;
	    top: 50%;
	    left: 50%;
	    margin: 0 0 0 -15px;
	    transition-duration: 0.2s;
		transform-origin: center center;
    	transform: rotate(-45deg);
	}
	&:after{
		content: '';
		width: 30px;
    	height: 1px;
	    background: #fff;
	    position: absolute;
	    top: 50%;
	    left: 50%;
	    margin: 0 0 0 -15px;
	    transition-duration: 0.2s;
		transform-origin: center center;
    	transform: rotate(45deg);
	}
	&:hover{
		opacity: 0.8;		
	}
}

.modal-content {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  pointer-events: auto;
  background-color: $color-1;
  background-clip: padding-box;
  box-shadow: 0 0 68px 0 rgba(0,0,0,0.61);
  border-radius: 10px;
  outline: 0;
  color: #fff;
  letter-spacing: 1px;
}

.modal-head{
	margin-bottom: 20px;
	padding: 0 10px;
	.logo-wrap{
		margin-top: -140px;
		.logo{
			max-width: 344px;
		}
	}
	h3, .h3{
		text-align: center;
		text-transform: uppercase;
		font-size: 20px;
		font-family: $font-1b;
		margin: -44px 0 0;
		text-shadow: 2px 2px 0 rgba(0,0,0,0.42);
		&.warning{
			color: $color-3;
		}
	}
}

.modal-body{
	padding: 0 68px 40px;
	a{
		color: #fff;
	}
	.form{
		max-width: 450px;
		margin: 0 auto;
	}
	.message{
		text-align: center;
	}
}

/* forms */
input[type='text'], input[type='tel'], input[type='search'], input[type='email'], input[type='password'], input[type='number'], input[type='date'], textarea, select{
	background: #fff;
	border: 1px solid #fff;
	margin: 0;
	width: 100%;
	height: 46px;
	padding: 0 25px;
	font-family: $font-1;
	font-size: 16px;
	border-radius: 23px;
	display: block;
	color: #464646;
}
textarea{
	padding: 10px 25px;
	height: 150px;
	resize: none;
}

::-webkit-input-placeholder {color:#464646;transition-duration:0.2s;}
::-moz-placeholder          {color:#464646;transition-duration:0.2s;}
:-moz-placeholder           {color:#464646;transition-duration:0.2s;}
:-ms-input-placeholder      {color:#464646;transition-duration:0.2s;}

:focus::-webkit-input-placeholder {opacity: 0.8;}
:focus::-moz-placeholder          {opacity: 0.8;}
:focus:-moz-placeholder           {opacity: 0.8;}
:focus:-ms-input-placeholder      {opacity: 0.8;}

.form-group{
	margin-bottom: 15px;
	position: relative;
	font-size: 15px;
	.valid-feedback{
		display: none;
	}
	&.is-invalid{
		input[type='text'], input[type='tel'], input[type='search'], input[type='email'], input[type='password'], input[type='number'], input[type='date'], textarea, select{
			border-color: #ff7f83;
		}
		.valid-feedback{
			display: block;
			position: absolute;
			top: 100%;
			left: 50%;
			margin-top: 3px;
			transform: translateX(-50%);
			background: #ff7f83;
			border-radius: 5px;
			color: #fff;
			text-align: center;
			padding: 4px 10px;
			font-size: 16px;
			line-height: 1.3em;
			z-index: 10;
			box-shadow: 2px 0 18px 0 rgba(0,0,0,0.4);
			&:before{
				content: '';
				position: absolute;
			    left: 50%;
			    bottom: 100%;
			    z-index: 3;
			    transform: translateX(-50%);
			    border: 8px solid transparent;
			    border-bottom: 7px solid #ff7f83;
			    border-top: none;
			    margin-top: -1px;
			}
		}
	}
	&.is-valid{
		&:after{
			content: '';
			position: absolute;
			right: 17px;
			top: 50%;
			width: 18px;
			height: 16px;
			transform: translateY(-50%);
			background: url(../images/valid.svg) center center no-repeat;
			background-size: contain;
			z-index: 5;
		}
	}
	&.date{
		&:after{
			content: '';
			position: absolute;
			right: 20px;
			top: 50%;
			width: 21px;
			height: 21px;
			transform: translateY(-50%);
			background: url(../images/icon-date.svg) center center no-repeat;
			background-size: contain;
			z-index: 5;
		}
	}
	&.checkbox{
		font-family: $font-1i;
	}
}

/* checkbox */
input[type='checkbox'], input[type='radio'] {
  vertical-align: top;
  width: 20px;
  height: 20px;
  margin: 0 3px 0 0;
}
input[type='checkbox'] + label, input[type='radio'] + label {
	cursor: pointer;
	padding-left: 32px;
	padding-top: 1px;
	position: relative;
	margin-bottom: 0px;
	display: block;
	line-height: 1.3em;
	&:before{
		content: '';
		position: absolute;
		left: 0px;
		top: 0;
		width: 20px;
  		height: 20px;
		border: 1px solid #fff;
		border-radius: 4px;
	}
	&:after{
		content: '';
		position: absolute;
		left: 3px;
		top: 3px;
	    width: 13px;
	    height: 13px;
		background: url(../images/check.svg) center center no-repeat;
		background-size: contain;
		opacity: 0;
	}
}
input[type='radio'] + label {
	&:before{
		border-radius: 50%;
	}
	&:after{
		margin: -4px 0 0 3px;
	    width: 9px;
	    height: 9px;
		border: 4px solid #000;
		background: #000;
		border-radius: 50%;
	}
}
input[type='checkbox']:not(checked), input[type='radio']:not(checked) {
	position: absolute;
	opacity: 0;
	left: 0;
	top: 0;
	z-index: -1;
}
input[type='checkbox']:checked + label, input[type='radio']:checked + label {
	&:after{
		opacity: 1;
	}
}

/* button */
.btn{
	display: inline-block;
	min-width: 308px;
	padding: 11px 25px;
	margin: 5px 0 10px;
	color: #fff;
	background-color: $color-3;
	border: 1px solid $color-3;
	font-size: 18px;
	font-family: $font-1b;
	text-decoration: none;
	text-transform: uppercase;
	text-align: center;
	cursor: pointer;
	border-radius: 26px;
	position: relative;
	z-index: 0;
	box-shadow: none;
	transition-duration: 0.4s;
	overflow: hidden;
	vertical-align: middle;

	&:focus{
		
	}
	&:hover{
		color: #fff;
		border-color: #b20005;
		background-color: #b20005;
		text-decoration: none;
	}

	&.btn-bordered{
		border: 1px solid #fff;
		background: none;
		&:hover{
			background: #fff;
			color: $color-1;
		}
	}
}

.recovery-link{
	text-align: right;
	margin-top: -5px;
	margin-bottom: 15px;
	font-size: 15px;
	font-family: $font-1i;
	a{
		border-bottom: 1px dashed #fff;
		transition: border 0.3s;
		&:hover{
			border-bottom-color: transparent;
			text-decoration: none;
		}
	}
}
.login-link{
	border-bottom: 1px solid #fff;
	transition: border 0.3s;
	&:hover{
		border-bottom-color: transparent;
		text-decoration: none;
	}
}

/* winners */
.tab-content > .tab-pane {
  display: none;
}
.tab-content > .active {
  display: block;
}
.winners{
	.nav{
		display: flex;
		position: relative;
		z-index: 1;
		padding: 0;
		margin: 0;
		list-style: none;
		a{
			display: flex;
			justify-content: center;
			align-items: center;
			width: 50%;
			height: 56px;
			padding: 5px 15px 10px;
			text-align: center;
			border-radius: 10px 10px 0 0;
			text-shadow: 1px 1px 0 rgba(0,0,0,0.61);
			&.active{
				background: $color-2;
			}
			img{
				display: block;
				margin: 0;
			}
			&:hover{
				text-decoration: none;
			}
		}
	}
	.tab-content{
		position: relative;
		z-index: 3;
		border-radius: 10px;
		margin-top: -5px;
		margin-bottom: 20px;
		background: $color-1;
		overflow: hidden;
		box-shadow: 0 0 68px 0 rgba(0,0,0,0.64);
		table{
			margin-bottom: 0;
		}
	}
}

/**/
.scroll-box{
	max-height: 376px;
	overflow: hidden;
}
.mCSB_scrollTools{
	top: 5px;
	bottom: 5px;
}
.mCSB_inside > .mCSB_container{
	margin-right: 0;
}
.mCSB_scrollTools{
  width: 6px;
}
.mCSB_outside+.mCS-minimal-dark.mCSB_scrollTools_vertical, .mCSB_outside+.mCS-minimal.mCSB_scrollTools_vertical {
    right: 0px;
    margin: 0px 0;
    opacity: 1;
}
.mCSB_scrollTools .mCSB_dragger {
  background: #fff;
  border-radius: 3px; }

.mCSB_outside + .mCS-minimal.mCSB_scrollTools_vertical, .mCSB_outside + .mCS-minimal-dark.mCSB_scrollTools_vertical {
  background: #fff;
  border-radius: 3px; }

.mCS-minimal.mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar, .mCS-minimal.mCSB_scrollTools .mCSB_draggerRail, .mCS-minimal-dark.mCSB_scrollTools .mCSB_draggerRail {
  background: none !important; }

/* myaccount */
.account-top{
	margin-bottom: 20px;
	display: flex;
	font-size: 16px;
	.my-info{
		margin: 0;
		padding: 0;
		width: 62%;
		li{
			display: flex;
			justify-content: space-between;
			align-items: center;
			padding: 4px 0;
			width: 100%;
			border-bottom: 1px solid rgba(256,256,256,0.32);
			.title{
				text-transform: uppercase;
				padding-right: 15px;
			}
			&:last-child{
				border-bottom: none;
			}
		}
	}
	.change-pass{
		display: flex;
		justify-content: flex-end;
		align-items: center;
		flex-grow: 1;
		padding-left: 15px;
		a{
			border-bottom: 1px dashed #fff;
			transition: border 0.3s;
			&:before{
				content: '';
				display: inline-block;
				vertical-align: top;
				width: 13px;
				height: 20px;
				background: url(../images/pass.svg) center center no-repeat;
				background-size: contain;
				margin-right: 5px;
			}
			&:hover{
				text-decoration: none;
				border-bottom-color: transparent;
			}
		}
	}
}
.my-statistic{
	border-radius: 10px;
	margin-bottom: 25px;
	background: $color-1;
	overflow: hidden;
	box-shadow: 0 0 68px 0 rgba(0,0,0,0.64);
	max-height: 309px;
	text-align: center;
	table{
		margin: 0;
	}
	.win{
		color: $color-3;
		text-shadow: 1px 1px 0px #fff, -1px -1px 0px #fff, 1px -1px 0px #fff, -1px 1px 0px #fff;
		font-family: $font-1ib;
	}
}

/* official-rules */
.modal .official-rules{
	margin-bottom: -35px;
	.scroll-box{
		margin-right: -58px;
	}
	.mCSB_container{
		padding-right: 58px;
	}
}
.official-rules{
	font-size: 16px;
	line-height: 1.65em;
	.scroll-box{
		max-height: 445px;
		position: relative;
		&:after{
			content: '';
			position: absolute;
			left: 0;
			bottom: 0;
			right: 0;
			height: 40px;
			background: -moz-linear-gradient(top, rgba(255,255,255,0) 0%, rgba(0,75,147,1) 100%);
			background: -webkit-linear-gradient(top, rgba(255,255,255,0) 0%,rgba(0,75,147,1) 100%);
			background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(0,75,147,1) 100%);
		}
	}
	.text-center{
		padding: 15px 0 10px;
	}
	h3{
		font-family: $font-1ib;
		font-size: 20px;
	}
	p+h3{
		margin-top: 25px;
	}
}
.account-message{
	text-align: center;
	font-family: $font-1i;
}

@media only screen and (max-width: 767px) {
	.modal-dialog {
		max-width: 300px;
		margin: 120px auto 0.5rem;
	}
	.modal button.close {
		top: 3px;
		right: 4px;
	}
	.modal-head{
		.logo-wrap{
			width: 248px;
		    height: 150px;
		    margin-top: -81px;
			.logo{
				width: 200px;
			}
		}
		h3, .h3{
			font-size: 16px;
			margin: -20px 0 0;
			line-height: 1.8em;
		}
	}
	.modal-body {
	    padding: 0 10px 20px;
	}

	/**/
	input[type='text'], input[type='tel'], input[type='search'], input[type='email'], input[type='password'], input[type='number'], input[type='date'], textarea, select{
		height: 40px;
	}
	.btn{
		min-width: 100%;
		padding: 5px 15px;
		margin: 0px 0 10px;
	}
	.form-group{
		margin-bottom: 10px;
		&.is-invalid{
			.valid-feedback{
				padding: 3px 5px;
				font-size: 13px;
			}
		}
	}
	.winners{
		.tab-content{
			margin-bottom: 10px;
			border: 1px solid rgba(256,256,256,0.32);
		}
	}
	.scroll-box{
		max-height: 250px;
	}

	.modal .official-rules{
		margin-bottom: -8px;
		.scroll-box{
			margin-right: -8px;
			max-height: 190px;
		}
		.mCSB_container{
			padding-right: 8px;
		}
	}
	.official-rules{
		font-size: 14px;
		line-height: 1.65em;
		.text-center{
			padding: 0px;
		}
		h3{
			margin-bottom: 0;
			margin-top: 0;
			font-size: 16px;
		}
	}

	/* myaccount */
	.account-top{
		flex-wrap: wrap;
		.my-info{
			margin: 0 0 15px;
			width: 100%;
			font-size: 14px;
			li{
				padding: 2px 0;
				span{
					white-space: nowrap;
				}
				.title{
					padding-right: 8px;
					white-space: normal;
				}
				&:last-child{
					border-bottom: 1px solid rgba(256,256,256,0.32);
				}
			}
		}
		.change-pass{
			justify-content: center;
			padding-left: 0;
		}
	}
	.my-statistic{
		margin-bottom: 15px;
		max-height: 271px;
		border: 1px solid rgba(256,256,256,0.32);
		table{
			margin: 0;
			th{
				padding: 12px 10px;
			}
			td{
				padding: 10px 10px;
			}
		}
	}
	.account-message{
		font-size: 16px;
	}
}