@import "variables";

/* second-screen */
.second-screen{
	background: url(../images/bg-2.jpg) center bottom no-repeat;
	background-size: cover;
	position: relative;
	.leaves{
		.item-l{
			left: 25px;
			top: 47px;
		}
		.item-r{
			right: -407px;
			top: -185px;
		}
	}

	.top-container{
		padding-bottom: 52px;
		min-height: 100vh;
		display: flex;
		flex-wrap: wrap;
	}
}

.second-screen-content{
	width: 100%;
	flex-grow: 1;
	display: flex;
	flex-direction: column;
	align-content: center;
}

.second-screen-head{
	text-align: center;
	color: #fff;
	font-size: 42px;
	font-family: $font-1b;
	line-height: 1.45em;
	text-transform: uppercase;
	text-shadow: 3px 3px 0 rgba(0,0,0,0.61);
	letter-spacing: 2px;
	width: 100%;
	margin: 0 auto 115px;
	position: relative;
	z-index: 15;
	
	&:before{
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 108px;
		z-index: 5;
		background: url(../images/board-1.png) center top no-repeat;
	}
	.animate-board{
		width: 100%;
		display: flex;
		justify-content: center;
		transform-origin: top center;
		animation-duration: 5s;
		animation-fill-mode: both;
		animation-iteration-count: infinite;
		animation-name: swing;
		padding-top: 37px;
		position: relative;
		z-index: 2;
		p{
			max-width: 460px;
			margin: 0;
		}
		&:after{
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 255px;
			z-index: -1;
			background: url(../images/board-2.png) center top no-repeat;
		}
	}
}

@keyframes swing {
  0% {
    transform: rotate3d(0, 0, 1, 1deg);
  }
  25% {
    transform: rotate3d(0, 0, 1, -1deg);
  }
  50% {
    transform: rotate3d(0, 0, 1, 1deg);
  }
  75% {
    transform: rotate3d(0, 0, 1, -1deg);
  }
  100% {
    transform: rotate3d(0, 0, 1, 1deg);
  }
}

/* prizes */
.prizes{
	margin-bottom: 104px;
	position: relative;
	padding: 0 10px;
	.prizes-carousel{
		display: flex;
		position: relative;
		height: 469px;
	}
	.prize-item{
		position: relative;
		text-align: center;
		padding: 70px 30px 30px;
		color: #fff;
		width: 417px;
		height: 469px;
		transition: transform 0.3s;
		z-index: 1;
		&:before{
			content: '';
			position: absolute;
			left: -31.65%;
			right: -31.65%;
			top: -28.14%;
			bottom: -28.14%;
			z-index: -1;
			background: url(../images/prize-bg.png) center center no-repeat;
			background-size: cover;
		}
		.mark{
			width: 58px;
			height: 58px;
			border-radius: 50%;
			background: rgba(256,256,256,0.2);
			box-shadow: 0 0 18px 0 rgba(0,0,0,0.4);
			position: absolute;
			left: 12px;
			top: 20px;
			font-size: 13px;
			font-family: $font-1ib;
			text-shadow: 3px 3px 0 rgba(54,54,54,0.61);
			line-height: 1.6em;
			padding: 11px 5px 5px 3px;
			.num{
				font-size: 30px;
				display: block;
				width: 100%;
			}
			&:after{
				content: '';
				position: absolute;
				top: -32px;
				right: -26px;
				width: 68px;
				height: 76px;
				background-repeat: no-repeat;
				background-position: center center;
				background-size: contain;
			}
			
		}
		.title{
			font-size: 35px;
			font-family: $font-1b;
			line-height: 1.3em;
			text-transform: uppercase;
			text-shadow: 3px 3px 0 rgba(54,54,54,0.61);
			margin: 0 0 20px;
		}
		.image{
			display: flex;
			justify-content: center;
			align-items: center;
			margin-bottom: 20px;
			img{
				width: auto;
				max-height: 210px;
			}
		}
		.desc{
			font-size: 15px;
			font-family: $font-1i;
			line-height: 1.65em;
			margin: 0;
			letter-spacing: 1.5px;
			strong{
				font-family: $font-1ib;
			}
			a{
				color: #fff;
				text-decoration: underline;
				&:hover{
					text-decoration: none;
				}
			}
			img{
				display: inline-block;
				margin: 7px 0 0;
				max-height: 33px;
			}
		}
		.hint{
			font-size: 18px;
		}
	}
	.key-1 .prize-item{
		.mark:after{
			background-image: url(../images/key-1.png);
		}
	} 
	.key-3 .prize-item{
		.image{
			margin-bottom: 5px;
			img{
				width: auto;
				max-height: 180px;
			}
		}
		.mark:after{
			background-image: url(../images/key-3.png);
		}
	} 
	.key-5 .prize-item{
		.mark:after{
			background-image: url(../images/key-5.png);
		}
	}

	.prize-item-wrap{
		padding: 0 50px;
		position: absolute;
		top: 0;
		transition: transform 0.2s, left 0.2s;
		&.first{
			left: 0;
			z-index: 5;
		}
		&.centered{
			left: 50%;
			transform: translateX(-50%);
			z-index: 3;
			.prize-item{
				transform: scale(1.3);
				.title{
					font-size: 32px;
				}
				.image{
					margin-bottom: 10px;
					img{
						max-height: 245px;
					}
				}
				.desc{
					font-size: 14px;
				}
			}
			&.key-3 .prize-item{
				.image{
					margin-bottom: 5px;
					img{
						width: auto;
						max-height: 180px;
					}
				}
			}
		}
		&.last{
			z-index: 1;
			left: 100%;
			transform: translateX(-100%);
		}
	}
}

.prizes-carousel-nav{
	position: absolute;
	left: 0;
	right: 0;
	top: 50%;
	margin-top: -30px;
	z-index: 5;
	height: 0;
	button{
		background: none;
		position: relative;
		width: 60px;
		height: 60px;
		display: block;
		border: none;
		transition: opacity 0.3s;
		position: absolute;
		top: 0;
		left: 50%;
		margin-left: -30px;
		transform: translateX(257px);
		&:before{
			content: '';
			display: block;
			width: 44px;
		    height: 44px;
		    border-top: 2px solid #fff;
		    border-right: 2px solid #fff;
		    transform: rotate(45deg);
		    transform-origin: center;
		    position: relative;
		    left: -10px;
		}
		&.prize-prev{
			transform: translateX(-257px);
			&:before{
				border-bottom: 2px solid #fff;
		    	border-left: 2px solid #fff;
		    	border-top: none;
		   		border-right: none;
		   		left: 20px;
			}
		}
		&:hover{
			opacity: 0.8;
		}
	}
}

.prize-hint{
	display: none;
}

.text-bottom{
	position: relative;
	z-index: 10;
	text-align: center;
	font-family: $font-1ib;
	font-size: 32px;
	letter-spacing: 1px;
	color: #fff;
	text-shadow: 3px 3px 0 rgba(54,54,54,0.61);
}

/* footer */
.footer{
	position: absolute;
	left: 0;
	right: 0;
	bottom: 0;
	padding: 15px 0;
	color: #fff;
	font-size: 15px;
	letter-spacing: 0.75px;
	a{
		color: #fff;
	}
	.row{
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	.sociale{
		display: flex;
		align-items: center;
		span{
			margin-right: 13px;
		}
		a{
			display: block;
			margin-right: 13px;
			max-width: 19px;
			transition: opacity 0.3s;
			img{
				display: block;
				height: 16px;
				max-width: 19px;
				max-height: 16px;
			}
			&:hover{
				opacity: 0.8;
			}
		}
	}
}

