@import "variables";

/* HEADER */
.logo-wrap{
	background: url(../images/logo-bg.png) center center no-repeat;
	background-size: cover;
	text-align: center;
	margin: 0 auto;
	width: 448px;
	height: 266px;
	display: flex;
	align-items: center;
	justify-content: center;
	span{
		display: block;
		width: 100%;
	}
}

.header{
	transition-duration: 0.2s;
	width: 130px;
	position: fixed;
	left: 0;
	top: 0;
	z-index: 1000;
	background: $color-1;
	padding: 6px 0 0;
	.logo-wrap{
		width: 100%;
		height: 89px;
		padding: 0 10px;
	}
	.tagline{
		text-align: center;
		font-family: $font-1ib;
		color: #fff;
		font-size: 14px;
		padding: 0 10px;
		width: 100px;
		display: block;
		margin: -17px auto 12px;
		line-height: 1.6em;
	}
}

/* NAV */
.nav-trigger{
	position: relative;
	width: 100%;
	height: 98px;
	border: none;
	background: none;
	cursor: pointer;
	text-align: center;
	margin: 0;
	display: block;
	font-family: $font-1ib;
	color: #fff;
	font-size: 14px;
	padding: 9px 5px 0;
	overflow: hidden;
	&:before{
		content: '';
		position: absolute;
		left: 0;
		top: 0px;
		right: 0;
		height: 1px;
		box-shadow: 0px 0px 3px 0 rgba(0,0,0,0.53);
	}
	svg{
		width: 51px;
		height: 34px;
		display: block;
		margin: 0 auto 5px;
	}
}

.main-menu{
	position: relative;
	z-index: 5;
	padding-top: 6px;
	position: fixed;
	top: 0;
	left: 0;
	width: 130px;
	height: 100vh;
	background: $color-1;
	transform: translateX(-100%);
	transition: transform 0.3s;
	.nav-trigger svg{
		width: 37px;
		height: 37px;
	}
	ul{
		margin: 0;
		padding: 0;
		list-style: none;
		position: relative;
		&:before{
			content: '';
			position: absolute;
			left: 0;
			bottom: 0px;
			right: 0;
			height: 1px;
			box-shadow: 0px 0px 3px 0 rgba(0,0,0,0.53);
		}
	}
	li{
		display: block;
		position: relative;
		a{
			display: block;
			font-size: 14px;
			font-family: $font-1i;
			line-height: 1.3em;
			text-align: center;
			padding: 25px 20px 15px;
			margin: 0;
			text-decoration: none;
			position: relative;
			color: #fff;
			overflow: hidden;
			background: $color-1;
			&:before{
				content: '';
				position: absolute;
				left: 0;
				top: -1px;
				right: 0;
				height: 1px;
				box-shadow: 0px 0px 3px 0 rgba(0,0,0,0.53);
			}
			.icon{
				width: 100%;
				margin-bottom: 6px;
				display: flex;
				justify-content: center;
				align-items: center;
				img{
					max-height: 47px;
					max-width: 60px;
				}
			}
		}
		&:nth-child(2) a .icon img{
			max-height: 37px;
		}
		&:nth-child(3) a .icon img{

		}
		&:nth-child(4) a .icon img{
			max-height: 33px;
		}
		&:nth-child(5) a .icon img{
			max-height: 36px;
		}
		&:hover > a, &.current-menu-item > a{
			background: $color-2;
		}
	}

	&.active{
		transform: translateX(0);
	}
}

@media (max-height: 880px) {
	.main-menu{
		li a{
			padding-top: 15px;
		}
	}
}
@media (max-height: 830px) {
	.main-menu{
		overflow-y: auto;
		li a{
			padding-top: 10px;
			padding-bottom: 10px;
			font-size: 13px;
			font-family: $font-1i;
			.icon{
				margin-bottom: 5px;
				img{
					max-height: 33px;
					max-width: 40px;
				}
			}
		}
	}
}
@media (max-height: 700px) {
	.main-menu{
		.logo-wrap{
			display: none;
		}
		.tagline{
			display: none;
		}
	}
}
@media (max-width: 991px) {
	.header{
		top: 10px;
		right: 0;
		left: auto;
		padding: 0;
		width: 75px;
		background: none;
		.logo-wrap{
			display: none;
		}
		.tagline{
			display: none;
		}
	}
	.nav-trigger{
		border-radius: 5px 0 0 5px;
		background-color: $color-1;
		height: 75px;
		&:before{
			display: none;
		}
	}
	.main-menu{
		right: 0;
		top: 9px;
		bottom: 0;
		height: auto;
		overflow-y: auto;
		width: 100%;
		padding: 0;
		background: rgba(0,75,147,0.97);
		z-index: 10000;
		.nav-trigger{
			padding-top: 0;
			float: right;
			width: 75px;
			svg{
				width: 37px;
				height: 37px;
				margin-bottom: 0;
			}
			span{
				display: none;
			}
		}
		.logo-wrap{
			display: none;
		}
		.tagline{
			display: none;
		}
		ul{
			overflow: hidden;
			clear: both;
			padding-top: 10px;
			&:before{
				bottom: auto;
				top: 0;
			}
		}
		li a{
			padding-top: 20px;
			padding-bottom: 20px;
			font-size: 14px;
			font-family: $font-1i;
			background: none;
			&:before{
				display: none;
			}
			.icon{
				margin-bottom: 5px;
				img{
					max-height: 47px;
					max-width: 60px;
				}
			}
		}
	}
}
@media (max-width: 767px) {
	.header{
		width: 50px;
	}
	.nav-trigger{
		height: 50px;
		font-size: 11px;
		padding-top: 3px;
		svg{
			width: 28px;
			height: 22px;
			margin-bottom: 0;
		}
	}
	.main-menu{
		.nav-trigger{
			width: 50px;
			svg{
				width: 18px;
				height: 18px;
			}
		}
	}
	
}