@mixin clearfix {
    &:after {
        content: "";
        display: table;
        clear: both;
    }
}

$color-1: #004b93;
$color-2: #003c75;
$color-3: #d30004;
$font-1: 'BookmanOldStyle', serif;
$font-1b: 'BookmanOldStyle-Bold', serif;
$font-1i: 'BookmanOldStyle-Italic', serif;
$font-1ib: 'BookmanOldStyle-BoldItalic', serif;
