/**/
*{
	padding: 0px;
	margin: 0px;
	box-sizing: border-box;
}
img{
	border:0;
	max-width:100%;
	height: auto;
}
a{
	color: #fff;
	text-decoration: none;
	transition: color 0.4s, background 0.4s;
}
a:hover{
	color: #fff;
	text-decoration: underline;
}
a:focus{
 	text-decoration: none;
}
*:focus,*:active, * {
	outline: none !important;
}
*::-moz-selection {
  background: rgba(211, 0, 4, 0.3); }

*::selection {
  background: rgba(211, 0, 4, 0.3); }

body{
	font: 18px $font-1;
	line-height: 1.5;
	color: #333333;
	background: $color-1; 
	padding: 0;
	overflow: hidden;
	&.loaded{
		overflow: auto;
	}
}

#wrapper{
	overflow: hidden;
	position: relative;
	transition: filter 0.2s;
}
.container{
	max-width: 1560px;
	padding: 0 15px;
	margin: 0 auto;
	position: relative;
	z-index: 10;
}

/* hint/tooltip */
.hint{
	cursor: pointer;
}
.tooltip{
	position: absolute;
	z-index: 1070;
	display: block;
	margin: 0 0 15px;
	opacity: 0;
	max-width: 370px;

	background: $color-1;
	border-radius: 5px;
	box-shadow: 2px 2px 84px 0 rgba(0,0,0,0.65);
	font-size: 14px !important;
	font-family: $font-1 !important;
	padding: 28px 25px;
	letter-spacing: 0.5px;
	line-height: 1.75em;
	text-align: center;
	color: #fff;
	&:before{
		content: '';
		position: absolute;
		left: 50%;
		bottom: -10px;
		transform: translateX(-50%);
		border: 12px solid transparent;
		border-top: 10px solid $color-1;
		border-bottom: none;
	}
	&.show {
		opacity: 1;
	}

	&.tooltip-stage{
		padding-right: 60px;
		text-align: left;
		max-width: 415px;
		&:after{
			content: '';
			position: absolute;
			right: -10px;
			top: 13px;
			width: 68px;
			height: 108px;
			background: url(../images/hint.png) right top no-repeat;
			-webkit-background-size: cover;
			background-size: cover;
		}
	}
}

.text-center{
	text-align: center;
}

/**/
h1, h2, h3, h4, h5, .h1, .h2, .h3, .h4, .h5{
	margin-top: 0px;
    margin-bottom: 0.8em;
}
h1, .h1{

}
h2, .h2{

}
h3, .h3{

}
h4, .h4{

}
h5, .h5{

}
h6, .h6{

}

strong{
	font-weight: normal;
	font-family: $font-1b;
}
em{
	font-style: normal;
	font-family: $font-1i;
}

/* table */
table{
	border-collapse: collapse;
	width: 100%;
	margin: 0px 0 15px;
	tbody tr:nth-child(2n+1){
		background: $color-2;
	}
	tbody tr:last-child{
		td{
			border-bottom: none;
		}
	}
}
table th{
	padding: 17px 30px;
	text-align: center;
	font-family: $font-1b;
	text-transform: uppercase;
	text-shadow: 2px 2px 0 rgba(0,0,0,0.42);
	color: #fff;
	font-size: 18px;
	line-height: 1.3em;
	border-bottom: 1px solid rgba(256,256,256,0.32);
}
table td{
	padding: 11px 30px 10px;
	font-size: 16px;
	line-height: 1.3em;
	border-bottom: 1px solid rgba(256,256,256,0.32);
}

@media (max-width: 767px){
	.tooltip{
		font-size: 13px !important;
		padding: 15px 15px;
		line-height: 1.6em;
		&.tooltip-stage{
			padding-right: 50px;
			max-width: 400px;
			&:after{
				right: -8px;
			    top: 2px;
			    width: 53px;
			    height: 82px;
			}
		}
	}
	table th{
		padding: 4px 7px;
		font-size: 18px;
	}
	table td{
		padding: 4px 7px;
		font-size: 13px;
	}
}
@media (max-width: 575px){
	.tooltip{
		font-size: 12px !important;
		padding: 10px 10px;
		line-height: 1.3em;
		&.tooltip-stage{
			padding-right: 10px;
			max-width: 300px;
			&:after{
				display: none;
			}
		}
	}
}